.navBg{
    background: #739072;
    color: white !important;
}

.btn-green{
    background: #688067;
    color: white !important;
    padding: 6px;
    outline: none;
    border: none;
    border-radius: 5px;
}

.btn-white{
    background: white;
    color: black !important;
    padding: 6px;
    outline: none;
    border: none;
    border-radius: 5px;
}

.navBgDark{
    background: rgb(27, 26, 26);
}

.darkContainer{
    background: rgb(41, 41, 41);
    color: white !important;
}

.darkTextArea{
    background: rgb(41, 41, 41);
    color: white !important;
}

.darkTextArea:focus{
    background: rgb(41, 41, 41);
    color: white !important;
}

.container-full{
    width: 100%;
    height: 100vh;
}

.icon{
    color: white;
    font-size: 20px;
}

.navbar-toggler{
    border: 1px solid white !important;
}

.alert{
    position: absolute;
    width: 100%;
    border-radius: 0;
    padding: 13px 15px;
}